import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import { Link } from "react-router-dom";
import {
  faSave,
  faTimesCircle,
  faCheckCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { uploadFile } from "react-s3";
import Loader from "src/loader/Loader";
import EVMaskedControl from "src/ev_masked_control/EVMaskedControl";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import classes from "src/helpers/classes_key";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { useHistory } from "react-router-dom";
const config = {
  bucketName: process.env.REACT_APP_BUCKET_NAME,
  dirName: "teacher_pics" + "/" + +new Date(),
  region: process.env.REACT_APP_REGION,
  accessKeyId: process.env.REACT_APP_ACCESS_ID,
  secretAccessKey: process.env.REACT_APP_ACCESS_KEY,
};

function edit_teacher(props) {
  let school_id = props.match.params.school_id;
  //alert(school_id)
  let [subjectList, setSubjectList] = useState([]);
  let [visible, setVisible] = useState(false);
  let teacher_id = props.match.params.teacher_id;
  let [teacher, setTeacher] = useState({});
  let [s_class, setSClass] = useState([]);
  let [dataLoaded, setDataLoaded] = useState(false);

  let history = useHistory();
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };

  // upload profile pic
  let uploadLogo = async (file) => {
    return uploadFile(file, config)
      .then((data) => {
        return data.key;
      })
      .catch((err) => console.error(err, ""));
  };

  // get country list from server
  useEffect(() => {
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/sm/get-subjects-by-school/${school_id}`
    )
      .then((response) => {
        let subjectArray = response.data.data;

        let data = subjectArray.map((sub) => {
          return {
            value: sub.subject_id,
            text: sub.subject_name,
          };
        });
        setSubjectList(data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);
  // get teacher detail from server
  useEffect(async () => {
    setDataLoaded(false);
    const res = await getTeacher();
    if (res) {
      setDataLoaded(true);
    }
  }, []);

  // get teacher data from database
  const getTeacher = async () => {
    try {
      await EDU_AXIOS.get(
        `${process.env.REACT_APP_API_PATH}/tm/get-teacher/${teacher_id}`
      )
        .then((resp) => {
          setTeacher(resp.data.data);
          const cls = resp.data.data.s_classes.map((val) => {
            return {
              text: val.text.toString(),
              value: val.value.toString(),
              selected: val.selected,
            };
          });
          setSClass(cls);
        })
        .catch((err) => {
          notify("error", err);
        });
      return true;
    } catch {
      return false;
    }
  };
  // gender array
  let gender = [
    { text: "Male", value: "Male" },
    { text: "Female", value: "Female" },
    { text: "Other", value: "Other" },
  ];
  let ph_str = `${teacher.mobile}`;
  let ph_st1 = ph_str.substr(0, 3);
  let ph_st2 = ph_str.substr(3, 3);
  let ph_st3 = ph_str.substr(6, 4);
  let ph_st_f = "+91" + " " + ph_st1 + " " + ph_st2 + " " + ph_st3;

  let eph_str = `${teacher.emg_no}`;
  let eph_st1 = eph_str.substr(0, 3);
  let eph_st2 = eph_str.substr(3, 3);
  let eph_st3 = eph_str.substr(6, 4);
  let eph_st_f = "+91" + " " + eph_st1 + " " + eph_st2 + " " + eph_st3;
  // formik params
  const ct = setTimeout(() => s_class, 100);
  let initialValues = {
    name: teacher.name,
    dob: teacher.f_dob,
    gender: teacher.gender,
    pan: teacher.pan,
    aadhar: teacher.aadhar,
    pic: "",
    picUp: [],
    email: teacher.email,
    phone: ph_st_f,
    emg_phone: eph_st_f,
    address: teacher.address,
    classes: [],
    primary_subject: teacher.primary_sub,
    secondary_subject: teacher.secondary_sub,
  };
  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    name: Yup.string()
      .min(3, "Name must have at least 3 characters")
      .max(500, "Name is too long")
      .required("Name is required")
      .matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed for this field "),
    username: Yup.string()
      .min(3, "Username must have at least 3 characters")
      .max(15, "Username is too long")
      .required("Username is required")
      .matches(
        /^[^@#]*[a-zA-Z0-9_.]+$/,
        "Only 'a-z,A-Z,0-9,undescre and dot' are allowed for this field "
      ),
    password: Yup.string()
      .min(6, "Password must be at least 6 characters")
      .max(15, "Password is too long")
      .required("Password is required"),
    repassword: Yup.string().oneOf(
      [Yup.ref("password"), null],
      "Passwords must match"
    ),
    dob: Yup.string(),
    gender: Yup.string(),
    email: Yup.string()
      .email("Invalid email address"),
      // .required("Email is required"),

    phone: Yup.string(),
      
      // .required("Phone is required"),
    emg_phone: Yup.string()
     ,
    //  .required("Phone is required"),
    address: Yup.string(),
    classes: Yup.array().min(1).of(Yup.string().trim().required()),
    primary_subject: Yup.string().required("Primary subject is required"),
  });
  // submit form
  const onSubmit = async (values, { resetForm }) => {
    let ph = values.phone.replace(/ +/g, "").slice(3);
    let emg_ph = values.emg_phone.replace(/ +/g, "").slice(3);
    if (values.picUp != "") {
      let upData = await uploadLogo(values.picUp);
      await EDU_AXIOS.post(
        process.env.REACT_APP_API_PATH + "/tm/edit-teacher/" + teacher_id,
        {
          name: values.name,
          email: values.email,
          gender: values.gender,
          mobile: ph,
          dob: values.dob,
          logo: upData,
          pan: values.pan,
          aadhar: values.aadhar,
          emg_no: emg_ph,
          classes: values.classes,
          primary_sub: values.primary_subject,
          secondary_sub: values.secondary_subject,
          address: values.address,
        }
      )
        .then(async (res) => {
          resetForm({ values: "" });
          history.push("/teacher-management/get-teachers/" + school_id);
          notify("success", res.data.message);
        })
        .catch((err) => {
          notify("error", err);
        });
    } else {
      await EDU_AXIOS.post(
        process.env.REACT_APP_API_PATH + "/tm/edit-teacher/" + teacher_id,
        {
          name: values.name,
          email: values.email,
          mobile: ph,
          dob: values.dob,
          gender: values.gender,
          logo: teacher.logo,
          pan: values.pan,
          aadhar: values.aadhar,
          emg_no: emg_ph,
          classes: values.classes,
          primary_sub: values.primary_subject,
          secondary_sub: values.secondary_subject,
          address: values.address,
        }
      )
        .then(async (res) => {
          resetForm({ values: "" });
          history.push("/teacher-management/get-teachers/" + school_id);
          notify("success", res.data.message);
        })
        .catch((err) => {
          notify("error", err);
        });
    }
  };
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Edit Teacher</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Update Teacher Detail</p>
          {dataLoaded ? (
            <Formik
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
              enableReinitialize={true}
            >
              {(formik) => (
                <Form className="row g-3">
                  <CRow className="mt-4">
                    <CCol md={12}>
                      <h6>Basic Information</h6>
                    </CCol>
                  </CRow>
                  <CCol md={6}>
                    <CFormLabel htmlFor="name" className="required-field">
                      Name
                    </CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="text"
                      name="name"
                      id="name"
                      value={formik.values.name}
                      placeholder="Teacher Name"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="dob" >
                      Date of Birth
                    </CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="date"
                      name="dob"
                      id="dob"
                      value={formik.values.dob}
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="gender">
                      Gender
                    </CFormLabel>
                    <br />
                    <EVFormikControl
                      control="radio"
                      name="gender"
                      inline={true}
                      options={gender}
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="pan">Pan</CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="text"
                      name="pan"
                      id="pan"
                      value={formik.values.pan}
                      placeholder="Pan"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="aadhar">Aadhar</CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="text"
                      name="aadhar"
                      value={
                        teacher.aadhar ? teacher.aadhar : formik.values.aadhar
                      }
                      id="aadhar"
                      placeholder="Aadhar"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="pic">Profile Picture</CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="file"
                      name="pic"
                      onChange={(e) => {
                        formik.setFieldValue("picUp", e.target.files[0]);
                      }}
                    />
                  </CCol>
                  <CRow className="mt-4">
                    <CCol md={12}>
                      <h6>Contact Information</h6>
                    </CCol>
                  </CRow>
                  <CCol md={6}>
                    <CFormLabel htmlFor="email" >
                      Email
                    </CFormLabel>
                    <EVFormikControl
                      control="input"
                      type="email"
                      name="email"
                      id="email"
                      value={formik.values.email}
                      placeholder="Email"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="phone" >
                      Mobile
                    </CFormLabel>
                    <EVMaskedControl
                      control="phone"
                      name="phone"
                      id="phone"
                      value={formik.values.phone}
                      placeholder="Mobile"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="emg_phone">
                      Emergency Mobile
                    </CFormLabel>
                    <EVMaskedControl
                      control="phone"
                      name="emg_phone"
                      id="emg_phone"
                      value={formik.values.emg_phone}
                      placeholder="Emergency Mobile"
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="phone" >
                      Address
                    </CFormLabel>
                    <EVFormikControl
                      control="textarea"
                      rows={1}
                      name="address"
                      id="address"
                      value={formik.values.address}
                      placeholder="Address"
                    />
                  </CCol>
                  <CRow className="mt-4">
                    <CCol md={12}>
                      <h6>Professional Information</h6>
                    </CCol>
                  </CRow>
                  <CCol md={6}>
                    <CFormLabel htmlFor="classes" className="required-field">
                      Classes
                    </CFormLabel>
                    <EVFormikControl
                      control="multiselect"
                      name="classes"
                      id="classes"
                      options={s_class}
                      placeholder="Select classes..."
                      selectAll={false}
                      onChange={() => s_class}
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel
                      htmlFor="primary_subject"
                      className="required-field"
                    >
                      Primary Subject
                    </CFormLabel>
                    <EVFormikControl
                      control="selectwithkey"
                      name="primary_subject"
                      id="primary_subject"
                      placeholder="Select subject..."
                      value={formik.values.primary_subject}
                      options={subjectList ? subjectList : []}
                    />
                  </CCol>
                  <CCol md={6}>
                    <CFormLabel htmlFor="secondary_subject">
                      Secondary Subject
                    </CFormLabel>
                    <EVFormikControl
                      control="selectwithkey"
                      name="secondary_subject"
                      id="secondary_subject"
                      placeholder="Select subject..."
                      value={formik.values.secondary_subject}
                      options={subjectList ? subjectList : []}
                    />
                  </CCol>
                  <CCardFooter>
                    <CButton block color="light" onClick={openModalBox}>
                      <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                    </CButton>
                    <CButton
                      block
                      color="dark"
                      type="submit"
                      disabled={formik.isSubmitting || !formik.isValid}
                      style={{ marginLeft: "15px" }}
                    >
                      {formik.isSubmitting ? (
                        <Loader />
                      ) : (
                        <>
                          <FontAwesomeIcon icon={faSave} /> Save
                        </>
                      )}
                    </CButton>
                  </CCardFooter>
                  <CModal visible={visible} onDismiss={closeModalBox}>
                    <CModalHeader onDismiss={closeModalBox}>
                      <CModalTitle>
                        <h5>Cancel</h5>
                      </CModalTitle>
                    </CModalHeader>

                    <CModalBody>Do You Want To Cancel ?</CModalBody>
                    <CModalFooter>
                      <CButton color="light" onClick={closeModalBox}>
                        <FontAwesomeIcon icon={faTimesCircle} /> No
                      </CButton>
                      <Link to="/">
                        <CButton color="dark">
                          <FontAwesomeIcon icon={faCheckCircle} /> Yes
                        </CButton>
                      </Link>
                    </CModalFooter>
                  </CModal>
                </Form>
              )}
            </Formik>
          ) : (
            <Loader />
          )}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
}
export default edit_teacher;
